import React from "react";
import { useDispatch, useSelector } from 'react-redux';
import * as actions from "../../redux/action/index";
import Navbar from "../Nav/NavBar";
import "./post.css"
import logo from './pokemon.png'

export default function Create() {
    document.getElementById('1').className = "e";
    const dispatch = useDispatch();
    var msgApi = useSelector((state) => state.resapimsg);
    var [inputipos, setInputipos] = React.useState("")
    var [arr, setArr] = React.useState([])
    const [input, setInput] = React.useState({ name: "", ataque: "", defensa: "", velocidad: "", peso: "", altura: "", img: "", types: arr })
    const [errors, setErrors] = React.useState({});

    
    const SaveTipo = () => {

        setInputipos(inputipos = document.getElementById("tipos").value)

        arr.push({ name: inputipos })

        setInputipos(document.getElementById("tipos").value = "", inputipos = "")

    }


    const InputChange = function (e) {
        setInput({
            ...input,
            [e.target.name]: e.target.value
        })
        setErrors(validate({
            ...input,
            [e.target.name]: e.target.value
        }));

    }

    return (
        <div >
            <Navbar />
            <h3>Crear nuevo pokemon</h3>
            <br />
           
            <form onSubmit={(e) => {
                e.preventDefault()
                console.log(arr)
                dispatch(actions.crearpokemos(input))
                setArr(
                    arr=[]
                )
            
                setInput({
                    ...input,
                     name: "", ataque: "", defensa: "", velocidad: "", peso: "", altura: "", img: "", types: arr
                })
               
            }} className="form">
                
                <label htmlFor="Name">Nombre </label>
                <br />
                <input type="text" name="name" id="Name"    className="inputs"  placeholder="insert nombre" onChange={InputChange} value={input.name} />
                <br />
                <strong className="error">{errors.name}</strong>
                <br />
                <label  htmlFor="Ataque">Ataque </label>
                <br />
                <input type="text" name="ataque" id="Ataque"   className="inputs"  placeholder="insert value" onChange={InputChange} value={input.ataque} />
                <br />
                <strong className="error">{errors.ataque}</strong>
                <br />
                <label htmlFor="Defensa">Defensa </label>
                <br />
                <input type="text" name="defensa" id="Defensa"    className="inputs" placeholder="insert value" onChange={InputChange} value={input.defensa} />
                <br />
                <strong className="error">{errors.defensa}</strong>
                <br />
                <label htmlFor="Velocidad">Velocidad </label>
                <br />
                <input type="text" name="velocidad"id="Velocidad"   className="inputs"  placeholder="insert value" onChange={InputChange} value={input.velocidad} />
                <br />
                <strong className="error">{errors.velocidad}</strong>
                <br />
                <label htmlFor="Peso">Peso </label>
                <br />
                <input type="text" name="peso" id="Peso"   className="inputs" placeholder="insert value" onChange={InputChange} value={input.peso} />
                <br />
                <strong className="error">{errors.peso}</strong>
                <br />
                <label htmlFor="Altura">Altura </label>
                <br />
                <input type="text" name="altura"id="Altura"   className="inputs"  placeholder="insert value" onChange={InputChange} value={input.altura} />
                <br />
                <strong className="error">{errors.altura}</strong>
                <br />
                <label htmlFor="Imagen">imagen </label>
                <br />
                <input type="url" name="img" id="Imagen"  className="inputs" placeholder="insert url de img" onChange={InputChange} value={input.img} />
                <br />
                <strong className="error">{errors.img}</strong>
                <br />
                <label htmlFor="Tipos">Tipo</label>
                <br />
                <input type="text" name="tipo" id="tipos" className="inputs" placeholder="insert tipo pokemon" /> <input type="button" value="Guardar tipo" className="boton" onClick={SaveTipo} />
                <br />
                <br />
                <br />
                {errors.img || errors.name || errors.altura || errors.peso || errors.defensa || errors.ataque || errors.velocidad ? <button disabled="disabled" className="buton1">Crear</button> : <button type="submit" className="buton">Crear</button>}   {msgApi.msg? <div id="gree">{msgApi.msg}</div>:<div></div> }
            </form>
            <img src={logo} alt="" className="imgpost"  width={"800px"} height={"600px"}/>

           
        </div>
    )


}

export function validate(input) {
    var letras = new RegExp("^[a-zA-Z]+$");
    var pesomax = new RegExp("^([0-9]){1,4}$");
    var number = new RegExp("^([0-9]){1,3}$");
    var alturargp = new RegExp("^([0-9]){1,2}$");
    var urlimg = new RegExp("(www|http:|https:)");
    let errors = {};

    if (!input.name) {
        errors.name = 'monbre del pokemon es requerido';
    } else if (!letras.test(input.name)) {
        errors.name = 'monbre del pokemon es invalido';
    }

    if (!input.ataque) {
        errors.ataque = 'valor de ataque requerido';
    } else if (!number.test(input.ataque)) {
        errors.ataque = 'valor de ataque invalido';
    }

    if (!input.defensa) {
        errors.defensa = 'valor de defensa requerido';
    } else if (!number.test(input.defensa)) {
        errors.defensa = 'valor de defensa invalido';
    }
    if (!input.velocidad) {
        errors.velocidad = 'valor de velocidad requerido';
    } else if (!number.test(input.velocidad)) {
        errors.velocidad = 'valor de velocidad invalido';
    }
    if (!input.peso) {
        errors.peso = 'valor de peso requerido';
    } else if (!pesomax.test(input.peso)) {
        errors.peso = 'valor de peso invalido';
    }

    if (!input.altura) {
        errors.altura = 'valor de altura requerido';
    } else if (!alturargp.test(input.altura)) {
        errors.altura = 'valor de altura invalido';
    }

    if (!input.img) {
        errors.img = 'url de imagen requerido';
    } else if (!urlimg.test(input.img)) {
        errors.img = 'url de imagen invalido';
    }
    return errors;
};

