import React from 'react';
import {connect } from 'react-redux';
import * as actions from "./../../redux/action/index";
import Navbar from "./../Nav/NavBar"
import './details.css';




class Details extends React.Component{
componentDidMount(){
    document.getElementById('1').className = "bg-c";
    const { match: { params } } = this.props;
    this.props.detalle(params.id)
}

componentWillUnmount(){
    const obj = {}
   this.props.limpiar(obj)
}
render(){
    return(
        <div>
         <Navbar />  
         {this.props.pot.pokemon? <div id="Detail">
                  
                 <h2>{ this.props.pot.pokemon.name }</h2>
                 <img src={this.props.pot.pokemon.img } alt="imagen" width={"460px"} height={"450px"}/>
                 {this.props.pot.tipo.map(e=><p>Tipo: {e.name?e.name:e.type.name}</p>)}
                 <p>Ataque: {this.props.pot.pokemon.ataque } </p>
                 <p>Defensa: {this.props.pot.pokemon.defensa } </p>
                 <p>Velocidad: {this.props.pot.pokemon.velocidad } </p>
                 <p>Peso: {this.props.pot.pokemon.peso }</p>
                 <p>Altura: {this.props.pot.pokemon.altura } </p>
                 <p>Número de Pokemon: {this.props.pot.pokemon.id} </p>
               
             </div>:<div></div>}
        
                  </div>
    )
}

}

export function mapStateToProps(state){
 return {
    pot:state.Detallepokemon
 }
}

export function mapDispatchToProps(dispatch){
    return {
       detalle:(id)=> dispatch(actions.getpokemonDetail(id)),
       limpiar: (e)=> dispatch( actions.limpiardetalle(e))

    } 
}

export default connect(mapStateToProps,mapDispatchToProps )(Details)






