import axios from "axios";
export const RCV_DATA = "RCV_DATA";
export const POKE_DETALLE = "POKE_DETALLE";
export const POKE_DETALLE1 = "POKE_DETALLE1";
export const POST_DATA = "POST_DATA";
export const RCV_DATA_API = "RCV_DATA_API";
export const QUERY = "QUERY";
export const FILTRO= "FILTRO";
export const FILTROTIPO= "FILTROTIPO";
export const ORDENAR="ORDENAR"
export const DELETE="DELETE"

console.log(process.env);
//obtener datos
export function crearpokemos(data) {
  return (dispatch) => {
    axios.post('/pokemons', data)
      .then(function (response) {
        dispatch({ type: POST_DATA, payload: response.data})
     
      })
      .catch(function (error) {
        dispatch({ type: POST_DATA, payload: error.response.data })
      });

  }
}
export function limpiardetalle(obj){
  return {type:POKE_DETALLE1, payload:obj  }
}
export function getpokemonDetail(id) {
  return (dispatch) => {
    axios({
      method: 'get',
      url: `/pokemons/${id}`
    }).then((params) => {
      dispatch({ type: POKE_DETALLE, payload: params.data })
    }).catch(e => console.log(e))
  }
}

export function obtenerdata() {

  return (dispatch) => {
    axios({
      method: 'get',
      url: "/pokemons",

    }).then((response) => {
      const array = []
      var data = response.data.filter(e => !e.url)
      if (data) { array.push(...data) }
      dispatch({ type: RCV_DATA, payload: array })

    })
      .catch((error) => {
        console.log(error);
      })

  }

};

export function obtenerpokeapi() {
  return (dispatch) => {
    axios({
      method: 'get',
      url: "/pokemons",

    }).then((response) => {
      const array = []

      var data = response.data.filter(e => e.url)


      data.map(async e => {
        await axios({
          method: 'get',
          url: e.url,
        }).then((resp) => {

          array.push({
              tipo: resp.data.types, pokemon: {
              id: resp.data.id,
              name: resp.data.name,
              ataque: resp.data.stats[1].base_stat,
              defensa: resp.data.stats[2].base_stat,
              velocidad: resp.data.stats[5].base_stat,
              peso: resp.data.weight,
              altura: resp.data.height,
              img: resp.data.sprites.other.dream_world.front_default
            }
          })

          dispatch({ type: RCV_DATA_API, payload: array });
        });
      });


    })
      .catch((error) => {
        console.log(error);
      })

  }

}

export function getQueryDetail(name) {
  return (dispatch) => {
    axios({
      method: 'get',
      url: `/pokemon?name=${name}`
    }).then((params) => {
      dispatch({ type: QUERY, payload:[params.data]})
    }).catch(e => console.log(e))
  }
}


//filtros y sorts
export function ordenar(arr){

  return (dispatch)=>{
    var idselect = document.getElementById('oapa').value; 
   switch (idselect) {
    case "ataquea":
      dispatch({ type: ORDENAR, payload:  Sorta(arr)})
      break;
      case "ataqued":
        dispatch({ type: ORDENAR, payload:  Sortd(arr)})
      break;
      case "alfabeticoa": 
      dispatch({ type: ORDENAR, payload: sortarregloa(arr) })
      break;
      case "alfabeticod": 
      dispatch({ type: ORDENAR, payload: sortarreglod(arr) })
      break;
    default: dispatch({ type: ORDENAR, payload: []})
      break;
   }
}
}
export function filtrado_por_tipo(params){
 return (dispatch)=>{

 var key = document.getElementById('tp').value; 

 dispatch({ type: FILTROTIPO, payload: tipo(params,key)})

 } 
}

export function filtrar(params) {
  return (dispatch) => {
  var response= null;
  var data = document.getElementById('pe').value; 
  switch (data) {
  case 'pokeApp': response = params.filter(e=>e.pokemon.id.length >3 ); break;
  case 'pokeApi': response= params.filter(e=>e.pokemon.id.toString().length <4);  break;
  case "1":response=[]; break;
  default: return response;
}
  dispatch({ type: FILTRO, payload:response})
  }
}


//callbacks
 
function Sorta(array) {

  if (array.length <= 1) { return array }
  const medio = array.shift();
  const ladomayor = []
  const ladomenor = []

  for (var i of array) {
      if (medio.pokemon.ataque > i.pokemon.ataque) {
          ladomenor.push(i)
      } else {
          ladomayor.push(i)
      }
  }
  return [...Sorta(ladomenor), medio, ...Sorta(ladomayor)]
}

function Sortd(array) {

  if (array.length <= 1) { return array }
  const medio = array.shift();
  const ladomayor = []
  const ladomenor = []

  for (var i of array) {
      if (medio.pokemon.ataque > i.pokemon.ataque) {
          ladomenor.push(i)
      } else {
          ladomayor.push(i)
      }
  }
  return [...Sortd(ladomayor), medio, ...Sortd(ladomenor)]
}
function tipo(array,select){
  var newarr=[]
  for (let i = 0; i < array.length; i++) {
      const element = array[i];
      for (let j = 0; j <  element.tipo.length; j++) {
          const element = array[i].tipo[j].type.name;
          if(element === select){
              newarr.push(array[i])
          }
      } 
  }
return newarr
}

function sortarregloa(arr){
  arr.sort((x,y)=>{
    if(x.pokemon.name < y.pokemon.name){
    return 1;
    }else if(x.pokemon.name > y.pokemon.name){
        return -1;
        }else {
            return 0
        }
})
return arr;
}
function sortarreglod(arr){
  arr.sort((x,y)=>{
    if(x.pokemon.name < y.pokemon.name){
    return -1;
    }else if(x.pokemon.name > y.pokemon.name){
        return 1;
        }else {
            return 0
        }
})
return arr;
}


