import './App.css';
import React from "react";
import inicio from "./components/paginainicio/Inicio";
import { Route } from "react-router-dom";
import Home from './components/Rutaprincipal/Home';
import Details from './components/detalle/details';
import Create from './components/crear/Post';

function App() {
  return (
    <React.Fragment>
      <Route exact path="/" component={inicio} />
      <Route path="/inicio" component={Home} />
      <Route path="/detalle/:id" component={Details} />
      <Route path="/crear" component={Create} />
    </React.Fragment>

  );
}

export default App;
