import React from "react";
import Card from "./Card";
import Paginado from "../paginado/paginado";
import * as actions from "../../redux/action/index"
import {  useDispatch, useSelector} from 'react-redux';
import Navbar from "./../Nav/NavBar"
import './Home.css'



export default function Home() {

document.getElementById('1').className = "e";
const dispatch = useDispatch()
const pokemon = useSelector((state) => state.pokemons)
const pokeapi = useSelector((state) => state.pokemonsPokeapi)
const fil = useSelector((state) => state.filtrado)

const [pagina,setPagina  ] = React.useState(1);
const porpagina =12;
var pokemons = [];
if(fil.length> 0 ){
    pokemons = [...fil];
}else{
     pokemons = [...pokemon,...pokeapi];
}
const maximo= Math.ceil(pokemons.length / porpagina);


React.useEffect(() => {
document.getElementById('1').className = "e";
dispatch(actions.obtenerdata())
dispatch(actions.obtenerpokeapi())
}, [dispatch])



return (<div> 
<Navbar />
<select name=" tipo de pokemon" id="tp" onChange={()=>dispatch(actions.filtrado_por_tipo(pokemons))}>
<option value={"1"}>tipo de pokemon</option>
<option value={"normal"}>normal</option>
<option value={"flying"}>flying</option>
<option value={"ground"}>ground</option>
<option value={"bug"}>bug</option>
<option value={"steel"}>steel</option>
<option value={"water"}>water</option>
<option value={"electric"}>electric</option>
<option value={"ice"}>ice</option>
<option value={"dark"}>dark</option>
<option value={"unknown"}>unknown</option>
<option value={"fighting"}>fighting</option>
<option value={"poison"}>poison</option>
<option value={"rock"}>rock</option>
<option value={"ghost"}>ghost</option>
<option value={"fire"}>fire</option>
<option value={"grass"}>grass</option>
<option value={"psychic"}>psychic</option>
<option value={"dragon"}>dragon</option>
<option value={"fairy"}>fairy</option>
<option value={"shadow"}>shadow</option>        
</select>
<select name="pokemon existente" id="pe" onChange={()=>dispatch(actions.filtrar(pokemons))}>
<option value={"1"}>pokemon existente</option>
<option value={"pokeApi"}>En pokeApi</option>
<option value={"pokeApp"}>En pokeApp</option>
</select>
<select name="orden alfabético y por ataque" id="oapa" onChange={()=>dispatch(actions.ordenar(pokemons))}>
<option value={"1"} >Ordenar</option>
<option value={"ataquea"}>ataque acendente</option>
<option value={"ataqued"}>ataque decendente</option>
<option value={"alfabeticoa"}>alfabetico acendente</option>
<option value={"alfabeticod"}>alfabetico decendente</option>
</select>

{pokemons?.slice((pagina -1) * porpagina, (pagina -1) * porpagina + porpagina).map((e,i) =>
<div key={i}>  
<Card  tipo={e.tipo} id={e.pokemon.id} name={e.pokemon.name} img={e.pokemon.img} /> 
</div>)
}
<br />
<Paginado pagina={pagina}  setPagina={setPagina}  maximo={maximo}/> 
</div>)


}


