import React from 'react';
import "./Card.css"
import { Link } from 'react-router-dom'
 

export default function Card({tipo, id, name, img}) {
 


  return ( <div id='card'> 
 
        <Link to={`/detalle/${id}`}><h3> {name}</h3></Link>  
        <img key={id} src={img} alt="deberia ir una imagen aqui" width={"200px"} height={"280px"} />
        {tipo.map(e=> <p>Tipo: {e.type.name}</p>)}
        </div> )
};