import {RCV_DATA,POKE_DETALLE,POKE_DETALLE1, POST_DATA, RCV_DATA_API,QUERY,FILTRO, FILTROTIPO, ORDENAR } from '../action/index'

const initialState = {
  pokemons:[],
  pokemonsPokeapi:[],
  Detallepokemon:{},
  filtrado:[],
  resapimsg:{},
  
};

const rootReducer = (state = initialState, action) => {
  switch (action.type) {
   case RCV_DATA: return {...state,pokemons:[...state.pokemons=action.payload]}
   case RCV_DATA_API: return { ...state,pokemonsPokeapi: [...state.pokemonsPokeapi=action.payload]}
   case POKE_DETALLE: return {...state, Detallepokemon:state.Detallepokemon=action.payload}
   case POKE_DETALLE1: return {...state, Detallepokemon:state.Detallepokemon=action.payload}
   case POST_DATA: return {...state, resapimsg: state.resapimsg=action.payload}
   case QUERY: return {...state,pokemons:state.pokemons.concat(action.payload) }
   case FILTRO: return { ...state,filtrado:[...state.filtrado=action.payload] } 
   case FILTROTIPO: return { ...state,filtrado:[...state.filtrado=action.payload] } 
   case ORDENAR: return { ...state,filtrado:[...state.filtrado=action.payload] } 
  
   default: return state;


  }

};

export default rootReducer;