import React from "react";
import './pagina.css'

export default function Paginado({ pagina, setPagina, maximo }) {
    const [pos, setPos] = React.useState(1);

    const sigtpage = () => {
        if (maximo > pos) {
            setPos(pos + 1)
            setPagina(pagina + 1)
        }
       
    }
    const antpage = () => {
        if (1 < pos) {
        setPos(pos - 1)
        setPagina(pagina - 1)
    }
}
    return (<div id="pasarpagina">
        <input type="button" value="<" onClick={() => antpage()} className="change"/> 
        <strong>{pos}</strong> 
        <p>de   {maximo}</p>
        <input type="button" value=">" onClick={() => sigtpage()} className="change"/>
    </div>
    )
}